import { Component, OnInit, ViewChild } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { LoadspecsService } from '../../_services/loadspecs.service';
import { AlertifyService } from '../../_services/alertify.service';
import { Supplier } from '../../_models/Supplier';

@Component({
  selector: 'app-manage-supplier',
  templateUrl: './manage-supplier.component.html',
  styleUrls: ['./manage-supplier.component.css']
})
export class ManageSupplierComponent implements OnInit {
  model: any = {};
  suppliers: Supplier[];
  roleid: any =  Number(localStorage.getItem('RoleID'));
  @ViewChild('infoModal') public infoModal: ModalDirective;

  constructor(private loadspecsService: LoadspecsService, private alertify: AlertifyService) { }

  ngOnInit(): void {
    this.loadSuppliers();
  }
  loadSuppliers() {
    return this.loadspecsService.loadSuppliers().subscribe((suppliers: Supplier[]) => {
      this.suppliers = suppliers;
      console.log(this.suppliers);
    }, error => {
      this.alertify.error(error);
    });
  }

  AddSupplier() {
    if (!this.model.text) {
      this.alertify.error('Enter Supplier');
      return;
    }
    this.loadspecsService.AddSupplier(this.model).subscribe(() => {
      this.model.text = '';
      this.alertify.success('Supplier Added Successfully');
      this.loadSuppliers();
      this.infoModal.hide();
    }, error => {
      this.alertify.error(error);
    });
  }

}
